@import url(https://fonts.googleapis.com/css2?family=Righteous&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Patrick+Hand&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Josefin+Sans:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Sigmar+One&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  margin: 0;
  padding: 0;
}

body {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  min-height: 100vh;
  font-family: "Josefin Sans", sans-serif;
  font-size: 22px;
  font-weight: 600;
  background: rgb(46, 193, 139);
  background: linear-gradient(
    90deg,
    rgba(46, 193, 139, 1) 35%,
    rgba(46, 193, 193, 1) 100%
  );
}

.quote {
  font-family: "Patrick Hand", cursive;
  font-size: 30px;
  letter-spacing: 2px;
}

.box {
  position: relative;
  width: 450px;
  height: 450px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  -webkit-flex-direction: column;
          flex-direction: column;
  background: white;
  box-shadow: 0 30px 60px rgba(0, 0, 0, 0.2);
  border-radius: 20%;
  overflow: hidden;
}

.box .percent {
  position: relative;
  width: 300px;
  height: 300px;
}

.box .percent svg {
  position: relative;
  width: 300px;
  height: 300px;
}

.box .percent svg circle {
  width: 300px;
  height: 300px;
  fill: none;
  stroke-width: 4;
  stroke: #202020;
  -webkit-transform: translate(0px, 0px);
          transform: translate(0px, 0px);
  stroke-dasharray: 240;
  stroke-dashoffset: 240;
}

.box .percent svg circle:nth-child(1) {
  stroke-dashoffset: 0;
  stroke: #d6d6d6;
}

#progress {
  stroke-dashoffset: calc(240 - (240 * 0) / 100);
  stroke: #2ec18b;
}

.box .percent .number {
  position: absolute;
  top: 0;
  left: 1;
  width: 100%;
  height: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  color: #202020;
}

.box .percent .number h2 {
  font-size: 60px;
}

.box .text {
  padding: 10px 0 0;
  color: #202020;
  font-weight: 700;
  letter-spacing: 1px;
}

.number {
  font-family: "Righteous", cursive;
}

.button-group {
  margin-top: 15px;
  width: 50%;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  -webkit-align-items: flex-start;
          align-items: flex-start;
}

.input-group {
  width: 300px;
}

.clock {
  padding-left: 5px;
  padding-right: 5px;
  color: #ffd100;
  background: #202020;
  border-radius: 5px;
}

.finished {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  color: #222;
  font-weight: 700;
  letter-spacing: 1px;
  font-size: 38px;
  font-family: 'Sigmar One', cursive;
  box-shadow: #202020;
}

.floating {
  position: relative;
  -webkit-animation: myfirst 5s linear 2s infinite alternate;
          animation: myfirst 5s linear 2s infinite alternate;
  z-index: -1;
}

.trybe_logo {
  position: absolute;
}

@-webkit-keyframes myfirst {
  0% {
    left: -300px;
    top: -100px;
  }

  20% {
    left: -250;
    top: 50px;
  }

  40% {
    left: -200px;
    top: 200px;
  }

  60% {
    left: -250px;
    top: 300px;
  }

  80% {
    left: -300px;
    top: 150px;
  }

  100% {
    left: -250px;
    top: 0px;
  }
}

@keyframes myfirst {
  0% {
    left: -300px;
    top: -100px;
  }

  20% {
    left: -250;
    top: 50px;
  }

  40% {
    left: -200px;
    top: 200px;
  }

  60% {
    left: -250px;
    top: 300px;
  }

  80% {
    left: -300px;
    top: 150px;
  }

  100% {
    left: -250px;
    top: 0px;
  }
}

